const save = (key, value) => {
  try {
    localStorage.setItem(key, JSON.stringify(value));
  } catch (error) {
    console.error('–ü–æ–º–∏–ª–∫–∞ –ø—Ä–∏ –∑–±–µ—Ä–µ–∂–µ–Ω–Ω—ñ –¥–∞–Ω–∏—Ö —É —Å—Ö–æ–≤–∏—â–µ:', error);
  }
};

const load = key => {
  try {
    const savedData = localStorage.getItem(key);
    return JSON.parse(savedData);
  } catch (error) {
    console.error('–ü–æ–º–∏–ª–∫–∞ –ø—Ä–∏ –æ—Ç—Ä–∏–º–∞–Ω–Ω—ñ –¥–∞–Ω–∏—Ö –∑—ñ —Å—Ö–æ–≤–∏—â–∞:', error);
    // return null;
  }
};

// export default { save, load };
export { save, load };
